@import './settings.scss';

.collapse {
    width: 100%;

    &__background--open {
        background-color: $secondary-background-color;
        
        
    }

    &__title {
        background-color: $main-color;
       
        display: flex;
        align-items: center;
        border-radius: 4px;
        padding: clamp(10px, 1.042vw, 15px);
        justify-content: space-between;
        z-index: 10;
        box-sizing: border-box;
        
        

        &__label {
            color: $tertiary-constrast-color;
            font-size: 18px;
            font-weight: 700;
            @media screen and (max-width: 480px) {
                font-size: 13px;
          
        }
    }

        &__handle,
        &__handle--open {
            width: clamp(16px, 1.667vw, 24px);
            transition: transform 100ms;
            margin: clamp(2px, 0.625vw, 9px);
            cursor: pointer;
            transform: rotate(180deg);
            

            &--open {
                transform: rotate(0deg);
                
            }
        }
    }

    &__hidden-box {
        transition: height 600ms;
        height: 0px;
        background-color: $secondary-background-color;
        overflow: hidden;
      

        * {
            z-index: -1;
            color: $main-color;
            font-size: $clamp-07-1rem;
            font-weight: 400;
            padding: 20px;
            line-height: 1.2rem;
            transform: translateY(-150px);
            transition: transform 600ms;
            

        }

        &--open {
            height: calc(116px + 2.778vw);
            position: relative;
            display: flex;
            z-index: 9;
            color: $footer-color;
            font-size: $clamp-07-1rem;
            font-weight: 400;
            padding: 20px;
            line-height: 1.2rem;
            transition: height 600ms;
            



            @media screen and (max-width: 768px) {
                height: calc(70px + 3.906vw);
                
            }

            @media screen and (max-width: 480px) {
                height: 165px;
                
            }

            * {
                transition: transform 0ms 5ms;
                transform: translateY(0px);
                z-index: 0;
                align-self: center;
                
            }

            ul {
                align-self: flex-start;
                transition: transform 0ms 5ms;
                transform: translateY(0px);
                z-index: 0;
                align-self: center;
                
            }
        }

        &__item {
            line-height: clamp(1rem, 1.8vw, 1.6rem);
            width: 100%;
            height: 100%;
            display: flex;
            gap: 50px;
            align-items: center;
            z-index: -10;
            
        }
    }
}